import React from "react"
import { Link, graphql, useStaticQuery } from 'gatsby'
import Header from "../components/header"
import Footer from "../components/footer"
import Welcome from "../components/welcome"
import Head from "../components/head"
import Form from "../components/contactform"
import styled from 'styled-components'
import "../styles/componentStyles/indexpage/theme.scss"
import * as newarticles from "../styles/componentStyles/mainpagearticles/newestarticles.module.scss"
import Img from 'gatsby-image'

const Main = styled.main`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  padding: 2em;
`

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    query {
      allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}){
        edges{
          node {
            frontmatter{
              title
              date(formatString: "DD.MM.YYYY")
              description
              banner {
                childImageSharp{
                  fluid(maxWidth: 1024){
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
            fields{
              slug
            }
          }
        }
      }
    }
  `)
  return (
    <div className="container">
      <Head title="Home" />
      <Welcome />
      <Header />
      <Main>
        <section className="theme-section">
          <div className="theme-section__contentbox">
            <h2 className="theme-section__title">
              Gry to Sztuka
        </h2>
            <p className="theme-section__content">
              Nie wierzycie w to? W momencie, gdy zastanawiamy si czym faktycznie sztuka jest, przed oczami kilka kategorii: obrazy, muzyka, wiersze, ksiki, filmy. Lista jest do duga, a tak naprawd to wszystko czy si ze sob w&nbsp;takim tworze jak gra komputerowa.
        </p>
            <p className="theme-section__content">
              Dobrze napisana fabua, przepikne widoki, klimatyczna muzyka te elementy skadaj si na cao zwan gr. Rozumimemy, e nie dla kadego jest to co oczywistego, ale patrzc obiektywnie, to taki jest stan rzeczy.
        </p>
            <p className="theme-section__content">
              Gry to wszystko w&nbsp;jednym pudeku
        </p>
          </div>
          <div className="theme-section__imagebox">

          </div>
        </section>
        <section className={newarticles.newestarticles}>
          <h2 className={newarticles.newestarticles__title}>
              Najnowsze<br />artykuy
          </h2>
          <div className={newarticles.newestarticles__list}>
            {
              data.allMarkdownRemark.edges.map((edge) => {
                return (
                  <Link to={`/blog/${edge.node.fields.slug}`} className={newarticles.article}>
                    <article className={newarticles.flex}>
                      <h2 className={newarticles.article__title}>
                        {edge.node.frontmatter.title}
                      </h2>
                      <p className={newarticles.article__date}>
                        {edge.node.frontmatter.date}
                      </p>
                      <div className={newarticles.article__content}>
                        <Img className={newarticles.article__image} fluid={edge.node.frontmatter.banner.childImageSharp.fluid} alt={edge.node.frontmatter.title} />
                        <p className={newarticles.article__text}>
                          {edge.node.frontmatter.description}
                        </p>
                      </div>
                        <span className={newarticles.article__readmore}>Czytaj wicej...</span>
                    </article>
                  </Link>
                )
              })
            }
          </div>
        </section>
      </Main>
      <Form />
      <Footer />
    </div>
  )
}

export default IndexPage
